.footer {
  background-color: #081130;
  padding: 20px 0 80px 0;

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__top-list {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    gap: 43px;

    li {
      flex: 1 1 auto;
    }

    @include mobile {
      gap: 20px;
    }
  }

  &__top-item {
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: var(--gray);

    @include mobile {
      font-size: 10px;
    }
  }

  &__text {
    margin-top: 22px;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: var(--gray);
  }
}
