.preparation {
  background-color: #081130;
  padding: 350px 0;
  height: 1352px;
  position: relative;

  @media(max-width: 1200px) {
    padding-top: 300px;
  }

  @include mobile {
    height: auto;
    padding-top: 90px;
  }

  &::before {
    content: "";
    background-image: url(../img/sky.png);
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 330px;

    @include mobile {
      height: 80px;
    }
  }

  &::after {
    content: "";
    background-image: url(../img/sky.png);
    background-size: cover;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 330px;
    transform: rotate(180deg);
    z-index: 5;

    @include mobile {
      height: 80px;
    }
  }

  &__container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 50px;

    @media(max-width: 1200px) {
      justify-content: flex-start;
    }
  }

  &__img {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 5;
    transform: translate(-10%, 0px);

    @media(max-width: 1700px) {
      transform: translate(-15%, -69px);
      width: 838px;
    }

    @media(max-width: 1200px) {
      right: 0;
      left: auto;
      width: 700px;
      transform: translate(0, 0);
    }

    @include mobile {
      width: auto;
    }
  }

  &__img-item {
    vertical-align: bottom;
  }

  &__content {
    position: relative;
    z-index: 10;
    max-width: 491px;
  }

  &__header {
    margin-bottom: 24px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 45px;
    line-height: 60px;
    color: var(--white);
    margin-bottom: 24px;

    @include small-tablet {
      font-size: 30px;
      line-height: 37px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 21px;
    line-height: 143%;
    color: var(--white);
    margin-bottom: 40px;

    @include small-tablet {
      font-size: 15px;
    }
  }

  &__btn {
    padding: 30px 50px;
    background-color: var(--orange);
    font-weight: 500;
    font-size: 18px;
    line-height: 152%;
    color: var(--black);
    border-radius: 8px;
    transition: 0.4s;
    border: 1px solid var(--orange);
    border-radius: 8px;

    &:hover {
      opacity: 0.6;
      border: 1px solid var(--black);
    }
  }
}
