.reviews {
  background-color: #081130;
  padding: 306px 0;
  position: relative;

  @include mobile {
    padding: 100px 0;
  }

  &::before {
    content: "";
    background-image: url(../img/sky.png);
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 330px;

    @include mobile {
      height: 80px;
    }
  }

  &::after {
    content: "";
    background-image: url(../img/sky.png);
    background-size: cover;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 330px;
    transform: rotate(180deg);
    z-index: 5;

    @include mobile {
      height: 80px;
    }
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 48px;
    line-height: 60px;
    text-align: center;
    color: var(--white);
    margin-bottom: 80px;
  }

  &__btn {
    justify-content: center;
    display: flex;
    gap: 25px;
    margin-top: 70px;
  }

  &__btn-arrow {
    width: 100%;
    height: 100%;
  }

  &__btn-item {
    display: inline-block;
    background-color: var(--orange);
    max-width: 66px;
    max-height: 66px;
    border-radius: 100px;
  }
}

.reviews-btn__next {
  transform: rotate(180deg);
}
