.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 47px 0 37px 0;
  border-bottom: 1px solid rgb(126, 134, 142, 0.1);
  z-index: 5;

  @include small-tablet {
    padding: 25px 0;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    flex-wrap: wrap;
  }

  &__info {
    display: flex;
    gap: 70px;
    align-items: center;

    @include small-tablet {
      padding-left: 50px;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__location {
    max-width: 151px;
  }

  &__city {
    margin: 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: var(--white);
    position: relative;

    @include small-tablet {
      font-size: 11px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -35px;
      display: inline-block;
      width: 17px;
      height: 17px;
      background-image: url(../img/loc.svg);
      background-size: cover;
    }
  }

  &__phone {
    max-width: 231px;
  }

  &__number {
    margin: 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 152%;
    color: var(--white);
    position: relative;

    @include small-tablet {
      font-size: 11px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 6px;
      left: -35px;
      display: inline-block;
      width: 19px;
      height: 19px;
      background-image: url(../img/phone.svg);
      background-size: cover;
    }
  }

  &__timetable {
    margin: 0;
    font-weight: 400;
    font-size: 13px;
    line-height: 150%;
    color: var(--white);

    @include small-tablet {
      font-size: 11px;
    }
  }
}
