.cook {
  padding: 5px 0;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__content {
    max-width: 580px;
  }

  &__header {
    margin-bottom: 24px;
    background-color: #F2F6FA;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 48px;
    line-height: 60px;
    color: var(--black);
    margin-bottom: 36px;

    @include small-tablet {
      font-size: 28px;
      line-height: 37px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 30px;
    max-width: 438px;
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--black);
    position: relative;
    padding: 0 0 0 22px;

    &::before {
      content: "";
      position: absolute;
      top: 7px;
      left: 0;
      width: 10px;
      height: 10px;
      display: inline-block;
      background-color: #5d6afb;
      border-radius: 50%;
      margin-right: 13px;

      @include mobile {
        width: 5px;
        height: 5px;
      }
    }

    @include mobile {
      font-size: 15px;
      padding: 0 0 0 10px;
    }
  }

  &__btn {
    border-radius: 8px;
    padding: 30px 50px;
    background-color: var(--orange);
    align-self: start;
    font-weight: 500;
    font-size: 18px;
    line-height: 152%;
    color: var(--black);
    transition: 0.4s;
    border: 1px solid var(--orange);
    border-radius: 8px;

    &:hover {
      opacity: 0.6;
      border: 1px solid var(--black);
    }
  }

  &__img {
    position: relative;

    @include tablet {
      align-self: center;
    }

    &::after {
      content: "";
      position: absolute;
      top: 135px;
      right: 0;
      background-image: url(../img/cook/1.svg);
      background-repeat: no-repeat;
      background-size: cover;
      display: inline-block;
      width: 275px;
      height: 266px;
      z-index: 2;
    }

    &::before {
      content: "";
      position: absolute;
      top: 130px;
      left: 0;
      background-image: url(../img/cook/2.svg);
      background-repeat: no-repeat;
      background-size: cover;
      display: inline-block;
      width: 275px;
      height: 266px;
      z-index: 2;
    }
  }

  &__img-item {
    position: relative;
    z-index: 10;
    min-width: 300px;
  }
}
