.products {
  background: var(--white);
  @include bg-cover;
  padding: 0 0 50px 0;


  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 48px;
    line-height: 60px;
    text-align: center;
    letter-spacing: -1px;
    color: var(--black);
    margin-bottom: 80px;

    @include small-tablet {
      font-size: 28px;
    }

    @include mobile {
      font-size: 21px;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
    gap: 20px;

    @include mobile {
      display: flex;
      flex-direction: column;
    }
  }
}
