.work {

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 48px;
    line-height: 60px;
    text-align: center;
    color: var(--black);
    margin-bottom: 14px;

    @include small-tablet {
      font-size: 28px;
      line-height: 39px;
    }

    @include mobile {
      font-size: 21px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 143%;
    text-align: center;
    color: var(--black);
    margin-bottom: 80px;

    @include mobile {
      font-size: 14px;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 9px;

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      display: flex;
      flex-direction: column;
    }
  }

  &__card {
    overflow: hidden;
  }

  &__img {
    vertical-align: bottom;
    width: 100%;
  }
}
