.contact {
  background-color: #081130;
  padding: 306px 0 150px 0;
  position: relative;

  @include mobile {
    padding: 100px 0;
  }

  &::before {
    content: "";
    background-image: url(../img/sky.png);
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 330px;

    @include mobile {
      height: 80px;
    }
  }

  &__content {
    background-color: var(--white);
    border-radius: 8px;
    padding: 70px 99px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include small-tablet {
      padding: 25px 15px;
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 36px;
    line-height: 133%;
    text-align: center;
    color: var(--black);
    margin-bottom: 12px;

    @include mobile {
      font-size: 19px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 21px;
    line-height: 143%;
    text-align: center;
    color: var(--black);
    margin-bottom: 35px;

    @include mobile {
      font-size: 14px;
    }
  }
}

.form {
  width: 100%;
  max-width: 780px;

  &__item {
    display: flex;
    align-items: center;
    gap: 20px;
    min-height: 85px;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__label {
    height: 100%;
    width: 100%;
  }

  &__input {
    background: #F2F6FA;
    border-radius: 8px;
    padding: 29px 5px 29px 41px;
    height: 100%;
    width: 100%;
    max-width: 380px;
  }

  &__btn {
    display: inline-block;
    background-color: var(--orange);
    border-radius: 8px;
    height: 100%;
    padding: 29px 29px 29px 41px;
    width: 100%;
    max-width: 380px;
  }

  &__checkbox {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 25px;
  }

  &__checkbox-input {
    width: 12px;
    height: 12px;
    border-radius: 2px;
  }

  &__checkbox-text {
    font-weight: 400;
    font-size: 11px;
    line-height: 130%;
    color: var(--black);
    margin-left: 8px;

    @include mobile {
      font-size: 10px;
    }
  }
}
