.hero {
  background-image: url(../img/hero-bg.jpg);
  @include bg-cover;
  padding: 307px 0 465px 0;
  position: relative;

  @include small-tablet {
    padding: 180px 0 150px 0;
  }

  @include mobile {
    padding-top: 350px;
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__content {
    max-width: 647px;
    position: relative;
    z-index: 2;
  }

  &__header {
    margin: 0;
    margin-bottom: 25px;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    color: var(--white);
    margin-bottom: 25px;

    @include small-tablet {
      font-size: 28px;
    }

    @include mobile {
      font-size: 21px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 143%;
    color: var(--white);
    margin-bottom: 40px;

    @include mobile {
      font-size: 14px;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__btn-item {
    background: var(--orange);
    border-radius: 8px;
    padding: 30px 50px;
    font-weight: 500;
    font-size: 18px;
    line-height: 152%;
    color: var(--black);
    transition: 0.3s;
    border: 1px solid var(--orange);
    border-radius: 8px;

    @include mobile {
      font-size: 13px;
      padding: 25px;
    }

    &:hover {
      opacity: 0.6;
    }
  }

  &__btn-descr {
    max-width: 150px;
    font-weight: 400;
    font-size: 15px;
    line-height: 160%;
    color: var(--white);

    @include mobile {
      color: var(--black);
      font-size: 11px;
    }
  }
}

.hero {
  &__decor-1 {
    width: 275px;
    height: 286px;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  &__decor-2 {
    width: 195px;
    height: 201px;
    position: absolute;
    top: 15%;
    left: -60px;
    transform: rotate(100deg);
  }

  &__decor-3 {
    width: 170px;
    height: 175px;
    position: absolute;
    top: 10%;
    right: -55px;
    transform: rotate(155deg);
  }

  &__decor-4 {
    width: 195px;
    height: 201px;
    position: absolute;
    bottom: 12%;
    right: 202px;
    transform: rotate(19deg);
  }
}

.decor {
  @include small-tablet {
    display: none;
  }
}
