.slider {
  display: flex;
  overflow: visible;

  @include small-tablet {
    overflow: hidden;
  }

  &__body {
    background-color: var(--white);
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: 0.5s;
    box-shadow: 2px 8px 16px rgba(194, 198, 208, 0.06);
    border-radius: 5px;
    padding: 55px 143px;
    height: 100%;
    transition: 0.3s;

    @include tablet {
      flex-direction: column;
      padding: 25px;
      gap: 50px;
    }
  }

  &__wrapper {
    align-items: center;
  }

  &__content {
    max-width: 460px;
  }

  &__decor {
    display: inline-block;
    width: 40px;
    height: 27px;
    background-image: url(../img/slider/decor.svg);
    margin-bottom: 30px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 30px;
    line-height: 143%;
    color: var(--black);
    margin-bottom: 24px;

    @include small-tablet {
      font-size: 21px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 170%;
    color: var(--black);
    margin-bottom: 24px;

    @include small-tablet {
      font-size: 13px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--blue);
  }

  &__user {
    max-width: 380px;
  }

  &__img {
    width: 100%;
    margin-bottom: 30px;
  }

  &__name {
    font-weight: 500;
    font-size: 21px;
    line-height: 143%;
    color: var(--black);
    margin-bottom: 6px;
    text-align: center;
  }

  &__loc {
    font-weight: 400;
    font-size: 16px;
    line-height: 170%;
    color: var(--black);
    opacity: 0.6;
    text-align: center;
  }
}

.swiper-slide {
  max-width: 1180px;
  opacity: 0.1;

  &.swiper-slide-active {
    opacity: 1;
  }

  &.swiper-slide-prev,
  &.swiper-slide-next {
    transition: 0.3s;
    transform: scale(0.9);
  }
}
