.card {

  &__item {
    background-color: var(--white);
    border: 1px solid #F2F6FA;
    border-radius: 0px 0px 10px 10px;
    transition: 0.3s;

    &:hover {
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

      .card__img-item {
        transform: scale(1.2);
      }
    }
  }

  &__img {
    overflow: hidden;
  }

  &__img-item {
    width: 100%;
    vertical-align: bottom;
    transition: 0.4s;
  }

  &__info {
    padding: 50px 44px;
  }

  &__descr {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 143%;
    color: var(--black);
    margin-bottom: 12px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--black);
    margin-bottom: 24px;
  }

  &__price {
    display: block;
    font-weight: 500;
    font-size: 24px;
    line-height: 150%;
    color: var(--black);
    margin-bottom: 14px;
  }

  &__btn {
    display: block;
    padding: 20px 40px;
    background-color: var(--orange);
    font-weight: 500;
    font-size: 18px;
    line-height: 152%;
    color: var(--black);
    transition: 0.4s;
    border: 1px solid var(--orange);
    border-radius: 8px;

    &:hover {
      opacity: 0.6;
      border: 1px solid var(--black);
    }
  }
}
